import React from 'react';

//import images
import EBSLogo from "../../../../assets/images/EBSLogo.png";

//import material ui
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";


class Welcome extends React.Component {
  constructor() {
    super();
    this.state = {
      componentName: "WelcomeComponent"
    }
  }
  render() {

    //check if translations was correctly loaded, -1 is an error
    if(this.props.translations == -1){
      throw new Error("Translation service was not correctly fetch");
    }

    return (
      <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
        style={{ padding: "20px" }}
      >
        <img style={{ height: "300px" }} src={EBSLogo} />
        <Typography variant="h3">EBS Sample manager</Typography>
        <Typography variant="h5">{this.props.translations.welcome}</Typography>
      </Grid>
    );
  }
}

export default Welcome;
