import React from 'react';

//import Material UI
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Fab from "@material-ui/core/Fab";
import Add from "@material-ui/icons/Add";
import Clear from "@material-ui/icons/Clear";
import Create from "@material-ui/icons/Create";

//import components
import Cart from "./batchEnhancedCart";

const styles = {
  appBar: {
    marginBottom: "0em"
  },
  rightSection: {
    marginLeft: 'auto',
    marginRight: 0
  },
  toolbarFab: {
    margin: "3px"
  }
}

class ToolBar extends React.Component {
  constructor() {
    super();
    this.state = {
      componentName: "requestToolBar",
      requestTableTicketCheckList: new Array(),
      cart: [],
      idCart: []
    }
  }
  render() {
    const actions = (
      <section>
        <Fab
          style={styles.toolbarFab}
          size="small"
          variant="contained"
          color="primary"
          onClick={this.props.addEvent}
        >
          <Add />
        </Fab>
        <Fab
          style={styles.toolbarFab}
          size="small"
          variant="contained"
          color="primary"
        >
          <Clear />
        </Fab>
        <Fab
          style={styles.toolbarFab}
          size="small"
          variant="contained"
          color="primary"
        >
          <Create />
        </Fab>
      </section>
    );
    return (
      <AppBar color="secondary" style={styles.appBar} elevation="0" position="static">
        <Toolbar variant="dense">
          <Typography variant="h5">{this.props.containerName}</Typography>
          <section style={styles.rightSection}>
            <Cart
              cart={this.props.cart}
              showReport={this.showTicketReport}
              translations={this.props.translations}
            />
          </section>
          {actions}
        </Toolbar>
      </AppBar>
    );
  }
}

export default ToolBar;
