import React from 'react';

//import Material UI
import Grid from '@material-ui/core/Grid';
import Button from "@material-ui/core/Button";
import CloseIcon from "@material-ui/icons/Close";
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from "@material-ui/core/IconButton";
import Dialog from '@material-ui/core/Dialog';
import Table from "@material-ui/core/Table";
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import { TableFooter } from '@material-ui/core';

//import catalogs
import cropCatalog from "../../../catalogs/cropCatalog";
import serviceCatalog from "../../../catalogs/serviceCatalog";
import sourceCatalog from "../../../catalogs/sourceCatalog";
import subServiceCatalog from "../../../catalogs/subServiceCatalog";
import tissueCatalog from "../../../catalogs/tissueCatalog";

const styles = {
  iconButton: {
    color: "white"
  },
  denseTableCell: {
    padding: "4px 5px 4px 5px"
  },
  toolbarFab: {
    margin: "3px"
  },
  tableHead: {
    position: "sticky",
    top: "5em",
    backgroundColor: "#DCDCDC",
    zIndex: 1,
    padding: "5px 0px 5px 0px",
    fontSize: ".8em"
  },
  tableFooter: {
    position: "sticky",
    bottom: -1,
    backgroundColor: "#DCDCDC",
    zIndex: 1,
  },
  modal: {
    width: "70vw"
  },
  dialogTitle: {
    position: "sticky",
    top: 0,
    backgroundColor: "white"
  },
  closeButton: {
    position: "absolute",
    right: "0px",
    top: "7px"
  },
  table: {
    height: "10px"
  }
}

class Report extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 0,
      data: []
    }
    this.handleChangePage = this.handleChangePage.bind(this);
  }

  handleChangePage(event, newPage) {
    console.log(newPage)
    this.setState({
      page: newPage
    });
  }

  render() {

    const columns = ["Ticket", "Id", "Service id", "Crop", "Subservice Id", "Comments", "Status Id"];

    const tableHead = (
      <TableHead>
        <TableRow>
          {columns.map(row => (
            <TableCell style={styles.tableHead}>{row}</TableCell>
          ))}
        </TableRow>
      </TableHead>
    );

    const tableBody = (
      <TableBody>
        {this.props.data.slice((this.state.page * 30), (this.state.page * 30) + 30).map(row => (
          <TableRow>
            <TableCell style={styles.denseTableCell}>{row.ticketNumber}</TableCell>
            <TableCell style={styles.denseTableCell}>{row.id}</TableCell>
            <TableCell style={styles.denseTableCell}>{serviceCatalog.get(Number(row.serviceId))}</TableCell>
            <TableCell style={styles.denseTableCell}>{cropCatalog.get(Number(row.cropId))}</TableCell>
            <TableCell style={styles.denseTableCell}>{subServiceCatalog.get(parseInt(row.subserviceId))}</TableCell>
            <TableCell style={styles.denseTableCell}>{row.comments}</TableCell>
            <TableCell style={styles.denseTableCell}>{row.statusId}</TableCell>
          </TableRow>
        ))}
      </TableBody>
    );

    const tableFooter = (
      <TableFooter>
        <TablePagination
          style={styles.tableFooter}
          rowsPerPageOptions={[30]}
          count={this.props.data.length}
          rowsPerPage={30}
          page={this.state.page}
          onChangePage={this.handleChangePage}
        />
      </TableFooter>
    )

    const report = (
      <Dialog
        onClose={this.props.handleClose}
        open={this.props.data.length > 0}
        aria-labelledby="simple-dialog-title"
        fullWidth={'true'}
        maxWidth={'lg'}
      >
        <DialogTitle style={styles.dialogTitle}>
          {this.props.title}
          <IconButton
            style={styles.closeButton}
            onClick={this.props.handleClose}
            variant="contained"
            color="secondary"
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <Table>
          {tableHead}
          {tableBody}
          {tableFooter}
        </Table>
      </Dialog>
    );

    return (
      <div>
        <Grid
          container
          direction="row"
          justify="flex-end"
          alignItems="center"
        >
        </Grid>
        {report}
      </div>
    );
  }
}

export default Report;
