import React from 'react';

//import clients
import clientBatch from "../../../clients/clientBatch/clientBatch";

//import components
import BatchTable from "./components/batchTable";
import ToolBar from "./components/batchToolBar";
import Loading from "../../components/loadingScreen";
import BatchForm from "./components/batchForm";

//create client objects
const client = new clientBatch();

class Shipment extends React.Component{
  constructor(){
    super();
    this.state = {
      componentName: "batchRequestContainer",
      data: [],
      requestTablePage: 0,
      dataSize: 0,
      rowsPerPage: 50,
      cart: [],
      idCart: [],
      checklist: new Array(),
      //state for form
      formOpen: false
    }
    this.createCheckList = this.createCheckList.bind(this);
    this.handleItemSelect = this.handleItemSelect.bind(this);
    this.clientRequestPutCall = this.clientRequestPutCall.bind(this);
    this.handlePageChange = this.handlePageChange.bind(this);
    this.handleFormClose = this.handleFormClose.bind(this);
    this.handleFormOpen = this.handleFormOpen.bind(this);
  }

  componentDidMount(){
    client.getBatches(res => {
      this.setState({
        data : res,
        dataSize : res.length
      })
      },0)
  }

  //Form methods
  handleFormClose(){
    this.setState({
      formOpen : false
    });
  }

  handleFormOpen(){
    this.setState({
      formOpen : true
    });
  }

  clientRequestPutCall() {
    this.setState({
      data: [],
      requestTablePage: 0,
      checklist: new Array(),
      cart: []
    });

    this.state.idCart.forEach(id => {
      clientRequestPut(res => {
        console.log(res)
        if (res == -1) {
          //i make this to summon an error scenario
          this.setState({
            data: -1
          });
        }
      }, Number(id));
    });

    //this timeout is to handle async issues until server accepts array of ids
    setTimeout(() => {
      clientRequest((res) => {
        this.setState({
          data: res,
          idCart: [],
          //this is hardcoded
          dataSize: 900
        }, this.createCheckList);
      }, this.state.requestTablePage);
    }, 780);
  }

  handleItemSelect(item, id) {
    let arr = { ...this.state.checklist };
    arr[id] = !arr[id];
    this.setState({
      checklist: arr
    });
    let itemArr = [...this.state.cart];
    if (itemArr.indexOf(item) === -1) {
      itemArr.push(item);
      this.setState({
        cart: itemArr
      }, () => { console.log(this.state.cart) });
    }
    else {
      let arr = itemArr.splice(itemArr.indexOf(item), 1);
      arr.sort();
      this.setState({
        cart: itemArr
      }, () => { console.log(this.state.cart) });
    }
    let idArr = [...this.state.idCart];
    if (idArr.indexOf(id) === -1) {
      idArr.push(id);
      this.setState({
        idCart: idArr
      }, () => { console.log(this.state.idCart) });
    }
    else {
      let arr = idArr.splice(idArr.indexOf(id), 1);
      arr.sort();
      this.setState({
        idCart: idArr
      }, () => { console.log(this.state.idCart) });
    }
  }

  createCheckList() {
    if (this.state.data.length > 0) {
      let arr = new Array();
      this.state.data.map(row => {
        arr[row.ticket] = false;
      });
      this.setState({
        checklist: arr
      });
    }
  }

  handlePageChange(page){
    console.log("current page: " + this.state.requestTablePage);
    console.log("new page: " + page);
    this.setState({
      data : [],
      requestTablePage : page
    }, () => {
      clientRequest(res => {
        this.setState({
          data : res
        })
      }, page);
    });
  }

  render() {
    //check if translations was correctly loaded, -1 is an error
    if (this.props.translations == -1) {
      throw new Error("Translation service was not correctly fetch");
    }
    if (this.state.data.length === 0) {
      return (
        <Loading translations={this.props.translations}/>
      );
    }
    else if (this.state.data.length > 0) {
      return (
        <div>
          <ToolBar 
            containerName = {this.props.translations.batch}
            cart = {this.state.cart}
            requestPut = {this.clientRequestPutCall}
            addEvent = {this.handleFormOpen}
          />
          <BatchForm
            open = {this.state.formOpen}
            onClose = {this.handleFormClose}
          />
          <BatchTable
            translations={this.props.translations}
            page={this.state.requestTablePage}
            dataSet={this.state.data}
            itemSelect={this.handleItemSelect}
            cart={this.state.cart}
            checklist = {this.state.checklist}
            dataSize={this.state.dataSize}
            rowsPerPage={this.state.rowsPerPage}
            requestPut={this.clientRequestPutCall}
            pageChange = {this.handlePageChange}
          />
        </div>
      );
    }
  }
}

export default Shipment;
