import React from 'react';

//import translations
import translations from "../translations/translations.json"

//import material ui
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepButton from '@material-ui/core/StepButton';
import Paper from "@material-ui/core/Paper";

//import containers
import Request from "../containers/request/request";
import Batch from "../containers/batch/batch";
import Design from "../containers/design/design";
import Shipment from "../containers/shipment/shipment";
import Welcome from "../containers/welcome/welcome"

//import static components
import AppBar from "../../../assets/html/components/AppBar";
import Navigation from "../components/appBar"

//import error boundaries
import GenericErrorBoundary from "../error/generalErrorBoundary";

//import clients
import clientTranslation from "../../clients/clientTranslation/client";
import { Typography } from '@material-ui/core';

//import theme
import { createMuiTheme } from '@material-ui/core/styles';
import { ThemeProvider } from '@material-ui/styles';

const theme = createMuiTheme({
  palette: {
    primary: {
      ligth: '#92c94b',
      main : "#77BC1F",
      dark: '#538315',
      contrastText: '#fff',
    },
    secondary: {
      ligth: '#77777b',
      main : "#55565A",
      dark: '#3b3c3e',
      contrastText: '#fff',
    },
    error: {
      main : "#9f1c37",
      contrastText: '#fff',
    }
  }
});

const styles = {
  staticHead : {
    marginBottom: ".2em"
  },
  content : {
    padding: ".7em"
  }
}

class Master extends React.Component {
  constructor() {
    super();
    this.state = {
      translations: {},
      tab: "welcome"
    }
    this.switchLanguage = this.switchLanguage.bind(this);
  }

  componentWillMount() {
    this.switchLanguage("spanish");
  }

  switchLanguage(language) {
    clientTranslation(res => {
      this.setState({
        translations : res
      });
    }, language);
  }

  switchTab(tab) {
    this.setState({
      tab: tab
    });
  }

  render() {

    const staticHead = (
      <Paper style={styles.staticHead}>
        <Navigation switchLanguage={this.switchLanguage}/>
        <Stepper>
          <Step><StepButton onClick={() => this.switchTab("request")} disabled={false} active={this.state.tab == "request"}>{this.state.translations.request}</StepButton></Step>
          <Step><StepButton onClick={() => this.switchTab("batch")} disabled={false} active={this.state.tab == "batch"}>{this.state.translations.batch}</StepButton></Step>
          <Step><StepButton onClick={() => this.switchTab("design")} disabled={false} active={this.state.tab == "design"}>{this.state.translations.design}</StepButton></Step>
          <Step><StepButton onClick={() => this.switchTab("shipment")} disabled={false} active={this.state.tab == "shipment"}>{this.state.translations.shipment}</StepButton></Step>
        </Stepper>
      </Paper>
    );

    let content = (null);

    switch (this.state.tab) {
      case "welcome":
        content = (
          <Welcome translations={this.state.translations}/>
        );
        break;
      case "request":
        content = (
          <Request translations={this.state.translations} />
        );
        break;
      case "batch":
        content = (
          <Batch translations={this.state.translations} />
        );
        break;
      case "design":
        content = (
          <Design translations={this.state.translations} />
        );
        break;
      case "shipment":
        content = (
          <Shipment translations={this.state.translations} />
        );
        break;
      default:
        break;
    }

    if(this.state.translations == {}){
      return(
        <Typography variant="h4">Loading...</Typography>
      );
    }

    return (
      <div>
        <ThemeProvider theme={theme}>
        <GenericErrorBoundary>
          {staticHead}
          <div style={styles.content}>
          {content}
          </div>
        </GenericErrorBoundary>
        </ThemeProvider>
      </div>
    );
  }
}

export default Master;
