import React from 'react';

//import Material UI
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableFooter from "@material-ui/core/TableFooter";
import TablePagination from "@material-ui/core/TablePagination";
import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';
import Button from "@material-ui/core/Button";

//import catalogs
import serviceCatalog from "../../../catalogs/serviceCatalog";
import subServiceCatalog from "../../../catalogs/subServiceCatalog";
import cropCatalog from "../../../catalogs/cropCatalog";

//import components
import EntityReport from "./enhancedReport";

//import clients
import clientRequestEntities from "../../../../clients/clientRequestEntities/client";

//styles
const styles = {
  tableCellStyle: {
    padding: "1px 3px 1px 3px"
  },
  tableHeadStyle: {
    position: "sticky",
    top: 0,
    backgroundColor: "#DCDCDC",
    zIndex: 1,
    padding: "5px 0px 5px 0px",
    fontSize: ".8em"
  },
  tableFooterStyle: {
    position: "sticky",
    bottom: -1,
    backgroundColor: "#DCDCDC",
    zIndex: 1,
  },
  tableStyle: {
    height: "65vh",
    overflowX: "hidden"
  }
}

class myTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      entityReport: [],
      error: false
    }

    this.headRows = [];

    this.showEntityReport = this.showEntityReport.bind(this);
    this.closeEntityReport = this.closeEntityReport.bind(this);
    this.handleChangePage = this.handleChangePage.bind(this);
  }

  handleChangePage(event, newPage) {
    this.props.pageChange(newPage);
  }


  showEntityReport(requestId) {
    clientRequestEntities(res => {
      if (res == -1) {
        console.error("entities fetch was not correct");
        this.setState({
          error : true
        });
      }
      this.setState({
        entityReport: res
      });
    }, requestId);
  }

  closeEntityReport() {
    this.setState({
      entityReport: []
    });
  }

  render() {
    if(this.state.error == true){
      throw new Error("Something has gone wrong");
    }
    const myarr = [
      this.props.translations.select,
      this.props.translations.ticket,
      this.props.translations.client,
      this.props.translations.service,
      this.props.translations.subService,
      this.props.translations.crop,
      this.props.translations.nEntities,
      this.props.translations.comments,
      this.props.translations.date
    ];

    const tableHead = (
      <TableHead>
        <TableRow>
          {myarr.map(row => (
            <TableCell style={styles.tableHeadStyle}>{row}</TableCell>
          ))}
        </TableRow>
      </TableHead>
    );

    const tableBody = (
      <TableBody>
        {this.props.dataSet.map((row) => (
          <TableRow selected={this.props.selectedTickets[row.ticketNumber]} key={row.ticketNumber}>
            <TableCell style={styles.tableCellStyle}><Checkbox color="primary" value={row.ticketNumber} checked={this.props.selectedTickets[row.ticketNumber]} onClick={() => this.props.ticketSelect(row.ticketNumber, row.id)}></Checkbox></TableCell>
            <TableCell style={styles.tableCellStyle}>{row.ticketNumber}</TableCell>
            <TableCell style={styles.tableCellStyle}>{row.clientFullName}</TableCell>
            <TableCell style={styles.tableCellStyle}>{serviceCatalog.get(Number(row.serviceId))}</TableCell>
            <TableCell style={styles.tableCellStyle}>{subServiceCatalog.get(Number(row.subserviceId))}</TableCell>
            <TableCell style={styles.tableCellStyle}>{cropCatalog.get(Number(row.cropId))}</TableCell>
            <TableCell style={styles.tableCellStyle}><Button onClick={() => this.showEntityReport(row.id)} variant="outlined" color="primary">{row.totalEntities}</Button></TableCell>
            <TableCell style={styles.tableCellStyle}>{row.comments}</TableCell>
            <TableCell style={styles.tableCellStyle}>{row.creationDate}</TableCell>
          </TableRow>
        ))}
      </TableBody>
    );

    const tableFoot = (
      <TableFooter>
        <TableRow>
          <TablePagination
            style={styles.tableFooterStyle}
            rowsPerPageOptions={[30]}
            colSpan={0}
            count={this.props.dataSize}
            rowsPerPage={this.props.rowsPerPage}
            page={this.props.page}
            onChangePage={this.handleChangePage}
          />
        </TableRow>
      </TableFooter>
    );

    return (
      <div>
        <EntityReport
          data={this.state.entityReport}
          handleClose={this.closeEntityReport}
          title = {this.props.translations.request}
        />
        <br></br>
        <Paper elevation={0}>
          <div>
            {/*This colgroup is set so headers and body match*/}
            <div style={styles.tableStyle}>
              <Table>
                {tableHead}
                {tableBody}
                {tableFoot}
              </Table>
            </div>
          </div>
        </Paper>
      </div>
    );
  }
}

export default myTable
