import React from 'react';

//import clients
import clientRequest from "../../../clients/clientRequest/client";
import clientRequestPut from "../../../clients/clientRequestPut/client";

//import components
import RequestTable from "./components/requestTable";
import ToolBar from "./components/toolBar";
import Loading from "../../components/loadingScreen";

class Table extends React.Component {

  constructor() {
    super();
    this.state = {
      componentName: "tableRequestContainer",
      data: [],
      requestTablePage: 0,
      dataSize: 900,
      rowsPerPage: 50,
      requestTableTicketCheckList: new Array(),
      ticketCart: [],
      idCart: []
    }
    this.createTicketCheckList = this.createTicketCheckList.bind(this);
    this.handleTicketSelect = this.handleTicketSelect.bind(this);
    this.clientRequestPutCall = this.clientRequestPutCall.bind(this);
    this.handlePageChange = this.handlePageChange.bind(this);
  }

  componentDidMount() {
    clientRequest((res) => {
      this.setState({
        data: res,
        //this is hardcoded
        dataSize: 900
      }, this.createTicketCheckList);
    }, this.state.requestTablePage);
  }

  clientRequestPutCall() {
    this.setState({
      data: [],
      requestTablePage: 0,
      requestTableTicketCheckList: new Array(),
      ticketCart: []
    });

    this.state.idCart.forEach(id => {
      clientRequestPut(res => {
        console.log(res)
        if (res == -1) {
          //i make this to summon an error scenario
          this.setState({
            data: -1
          });
        }
      }, Number(id));
    });

    //this timeout is to handle async issues until server accepts array of ids
    setTimeout(() => {
      clientRequest((res) => {
        this.setState({
          data: res,
          idCart: [],
          //this is hardcoded
          dataSize: 900
        }, this.createTicketCheckList);
      }, this.state.requestTablePage);
    }, 780);
  }

  handleTicketSelect(ticket, id) {
    let arr = { ...this.state.requestTableTicketCheckList };
    arr[ticket] = !arr[ticket];
    this.setState({
      requestTableTicketCheckList: arr
    });
    let tickArr = [...this.state.ticketCart];
    if (tickArr.indexOf(ticket) === -1) {
      tickArr.push(ticket);
      this.setState({
        ticketCart: tickArr
      }, () => { console.log(this.state.ticketCart) });
    }
    else {
      let arr = tickArr.splice(tickArr.indexOf(ticket), 1);
      arr.sort();
      this.setState({
        ticketCart: tickArr
      }, () => { console.log(this.state.ticketCart) });
    }
    let idArr = [...this.state.idCart];
    if (idArr.indexOf(id) === -1) {
      idArr.push(id);
      this.setState({
        idCart: idArr
      }, () => { console.log(this.state.idCart) });
    }
    else {
      let arr = idArr.splice(idArr.indexOf(id), 1);
      arr.sort();
      this.setState({
        idCart: idArr
      }, () => { console.log(this.state.idCart) });
    }
  }

  createTicketCheckList() {
    if (this.state.data.length > 0) {
      let arr = new Array();
      this.state.data.map(row => {
        arr[row.ticket] = false;
      });
      this.setState({
        requestTableTicketCheckList: arr
      });
    }
  }

  handlePageChange(page){
    console.log("current page: " + this.state.requestTablePage);
    console.log("new page: " + page);
    this.setState({
      data : [],
      requestTablePage : page
    }, () => {
      clientRequest(res => {
        this.setState({
          data : res
        })
      }, page);
    });
  }

  render() {
    //check if translations was correctly loaded, -1 is an error
    if (this.props.translations == -1) {
      throw new Error("Translation service was not correctly fetch");
    }
    
    if (this.state.data.length === 0) {
      return (
        <Loading translations={this.props.translations}/>
      );
    }
    else if (this.state.data.length > 0) {
      return (
        <div>
          <ToolBar 
            containerName={this.props.translations.request}
            ticketSelect={this.handleTicketSelect}
            selectedTickets={this.state.requestTableTicketCheckList}
            ticketCart={this.state.ticketCart}
            requestPut={this.clientRequestPutCall}
          />
          <RequestTable
            translations={this.props.translations}
            page={this.state.requestTablePage}
            dataSet={this.state.data}
            ticketSelect={this.handleTicketSelect}
            selectedTickets={this.state.requestTableTicketCheckList}
            ticketCart={this.state.ticketCart}
            dataSize={this.state.dataSize}
            rowsPerPage={this.state.rowsPerPage}
            requestPut={this.clientRequestPutCall}
            pageChange = {this.handlePageChange}
          />
        </div>
      );
    }
  }
}

export default Table;
