//import models
import batchModel from "./batchModel";

class ClientBatch {
  constructor() {
    this.getBatches = this.getBatches.bind(this);
  }
  getBatches(callBack, page) {
    let url = "http://172.17.60.49:8290/api-sm/v0.1/sample-batches?cropId=0&serviceId=0&subserviceId=0&page=" + page;
    console.log(url);

    let dataSet = [];
  
    var myHeaders = new Headers();
  
    myHeaders.append('Accept', 'application/json');
  
    var myInit = {
      method: 'GET',
      headers: myHeaders,
    };

    fetch(url, myInit)
      .catch(error => {
        return (error);
      })
      .then(res => {
        console.log(res.status);
        if(res.status != 200){
          callBack(-1)
        }
        else{
          return (res.json());
        }
      })
      .then(res => {
        try {
          res.response.batches.forEach(element => {
            let model = new batchModel(
              element.creation_date,
              element.description,
              element.id,
              element.entities.requests
            );
            dataSet.push(model);
          });       
          console.log(dataSet);
          callBack(dataSet);   
        } catch (error) {
          callBack(-1)
        }
      })

  }
}

export default ClientBatch;
