import React, { Component } from 'react'

//import Material UI
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';

//import Material icons
import ErrorIcon from '@material-ui/icons/Settings';

class ErrorBoundary extends Component {
constructor(props) {
 super(props)
  this.state = {
    hasError: false,
 }
}
static getDerivedStateFromError(error) {
 return {
  hasError: true
 }
}
componentDidCatch(error, info) {
  // You can also log the error to an error reporting service
}
render() {
 if (this.state.hasError) {
  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth="lg" style={{paddingTop: "80px"}}>
        <Typography align="center">
          <ErrorIcon className="Gear-icon-right" />
          <ErrorIcon className="Gear-icon-left" />
        </Typography>
        <Typography align="center" variant="h3">An unexpected error has occurred</Typography>
        <Typography align="center" variant="h6">Please contact system administrator</Typography>
      </Container>
    </React.Fragment>
  );

 }
 return this.props.children
 }
}
export default ErrorBoundary
