import React from 'react';

//import Material UI
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableFooter from "@material-ui/core/TableFooter";
import TablePagination from "@material-ui/core/TablePagination";
import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';
import Button from "@material-ui/core/Button";

//import components
import Report from "./batchEnhancedReport";

//styles
const styles = {
  tableCellStyle: {
    padding: "1px 3px 1px 3px"
  },
  tableHeadStyle: {
    position: "sticky",
    top: 0,
    backgroundColor: "#DCDCDC",
    zIndex: 1,
    padding: "5px 0px 5px 0px",
    fontSize: ".8em"
  },
  tableFooterStyle: {
    position: "sticky",
    bottom: -1,
    backgroundColor: "#DCDCDC",
    zIndex: 1,
  },
  tableStyle: {
    height: "65vh",
    overflowX: "hidden"
  }
}

class myTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      Report: [],
      error: false,
    }

    this.headRows = [];

    this.showReport = this.showReport.bind(this);
    this.closeReport = this.closeReport.bind(this);
    this.handleChangePage = this.handleChangePage.bind(this);
  }

  handleChangePage(event, newPage) {
    this.props.pageChange(newPage);
  }

  showReport(data) {
    this.setState({
      Report: data
    });
  }

  closeReport() {
    this.setState({
      Report: []
    });
  }

  render() {
    if (this.state.error == true) {
      throw new Error("Something has gone wrong");
    }
    const myarr = [
      "Select",
      "Creation date",
      "Description",
      "Entities",
      "Id"
    ];

    const tableHead = (
      <TableHead>
        <TableRow>
          {myarr.map(row => (
            <TableCell style={styles.tableHeadStyle}>{row}</TableCell>
          ))}
        </TableRow>
      </TableHead>
    );

    const tableBody = (
      <TableBody>
        {this.props.dataSet.map((row) => (
          <TableRow selected={this.props.checklist[row.id]} key={row.id}>
            <TableCell style={styles.tableCellStyle}><Checkbox color="primary" value={row.ticketNumber} onClick={() => this.props.itemSelect(row.description, row.id)}></Checkbox></TableCell>
            <TableCell style={styles.tableCellStyle}>{row.creation_date}</TableCell>
            <TableCell style={styles.tableCellStyle}>{row.description}</TableCell>
            <TableCell style={styles.tableCellStyle}><Button onClick={() => this.showReport(row.entities)} variant="outlined" color="primary">{row.entities.length}</Button></TableCell>
            <TableCell style={styles.tableCellStyle}>{row.id}</TableCell>
          </TableRow>
        ))}
      </TableBody>
    );

    const tableFoot = (
      <TableFooter>
        <TableRow>
          <TablePagination
            style={styles.tableFooterStyle}
            rowsPerPageOptions={[50]}
            colSpan={0}
            count={this.props.dataSize}
            rowsPerPage={this.props.rowsPerPage}
            page={this.props.page}
            onChangePage={this.handleChangePage}
          />
        </TableRow>
      </TableFooter>
    );

    return (
      <div>
        <Report
          data={this.state.Report}
          handleClose={this.closeReport}
          title={this.props.translations.batch}
        />
        <br></br>
        <Paper elevation={0}>
          <div>
            {/*This colgroup is set so headers and body match*/}
            <div style={styles.tableStyle}>
              <Table>
                {tableHead}
                {tableBody}
                {tableFoot}
              </Table>
            </div>
          </div>
        </Paper>
      </div>
    );
  }
}

export default myTable
