import { networkInterfaces } from "os";

class ClientRequestModel {
  constructor(comments, creationDate, cropId, firstName, id, lastName, serviceId, subserviceId, ticketNumber, totalEntities, userId) {
    this._comments = comments,
    this._creationDate = creationDate,
    this._cropId = cropId,
    this._firstName = firstName,
    this._id = id,
    this._lastName = lastName,
    this._serviceId = serviceId,
    this._subserviceId = subserviceId,
    this._ticketNumber = ticketNumber,
    this._totalEntities = totalEntities,
    this._userId = userId
  }
  get comments(){
    return this._comments;
  }
  set comments(newComments){
    this._comments = newComments;
  }
  get creationDate(){
    return this._creationDate;
  }
  set creationDate(newCreationDate){
    this._creationDate = newCreationDate;
  }
  get cropId(){
    return this._cropId;
  }
  set cropId(newCropId){
    this._cropId = newCropId;
  }
  get firstName(){
    return this._firstName;
  }
  set firstName(newFirstName){
    this._firstName = newFirstName;
  }
  get id(){
    return this._id;
  }
  set id(newId){
    this_.id = newId;
  }
  get lastName(){
    return this._lastName;
  }
  set lastName(newLastName){
    this._lastName = newLastName;
  }
  get serviceId(){
    return this._serviceId;
  }
  set serviceId(newServiceId){
    this._serviceId = newServiceId;
  }
  get subserviceId(){
    return this._subserviceId;
  }
  set subserviceId(newSubserviceId){
    this._subserviceId = newSubserviceId;
  }
  get ticketNumber(){
    return this._ticketNumber;
  }
  set ticketNumber(newTicketNumber){
    this._ticketNumber = newTicketNumber;
  }
  get totalEntities(){
    return this._totalEntities;
  }
  set totalEntities(newTotalEntities){
    this._totalEntities = newTotalEntities;
  }
  get userId(){
    return this._userId;
  }
  set userId(newUserId){
    this._userId = newUserId;
  }
  get clientFullName(){
    return this._firstName + " " + this.lastName;
  }
}

export default ClientRequestModel;
