import React from 'react';

//import Material UI
import AppBar from '@material-ui/core/AppBar';
import Typography from '@material-ui/core/Typography';
import Toolbar from '@material-ui/core/Toolbar';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Button from "@material-ui/core/Button";

//import Material Icons
import LanguageIcon from "@material-ui/icons/Language"
import AccountCircle from '@material-ui/icons/AccountCircle';

//import images
import SpainFlag from "../../../assets/images/Spain.png";
import UKFlag from "../../../assets/images/United-Kingdom.png";
import CIMMYTWhiteLogo from  "../../../assets/images/156122533288812689.png";

const styles = {
  flagIconStyle : {
    width : "3em"
  },
  rightSection : {
    marginLeft: 'auto',
    marginRight: -12
  },
  CIMMYTLogo : {
    height: "45px",
    marginRight: "10px"
  }
}

class Navigation extends React.Component {
  constructor() {
    super();
    this.state = {
      AnchorEl: undefined,
      open: false
    }
    this.handleLanguageOpen = this.handleLanguageOpen.bind(this);
    this.handleLanguageClose = this.handleLanguageClose.bind(this);
  }

  handleLanguageOpen() {
    this.setState({
      open: true,
      anchorEl: event.currentTarget
    });
  }

  handleLanguageClose() {
    this.setState({
      open: false,
    });
  }

  render() {
    return (
      <AppBar color="primary" position="static">
        <Toolbar>
          <img style={styles.CIMMYTLogo} src={CIMMYTWhiteLogo}/>
          <Typography variant="h5">EBS Sample Manager</Typography>
          <section style={styles.rightSection}>
            <Button
              style={{color: "white"}}
              onClick={this.handleLanguageOpen}
            >
              <LanguageIcon fontSize="default"/>
            </Button>

            <Button
              style={{color: "white"}}
              onClick={ ()=> window.location.href="/logout"}
            >
              <AccountCircle fontSize="default"/>
            </Button>

            <Menu
              getContentAnchorEl={null}
              id="simple-menu"
              open={this.state.open}
              onClose={this.handleLanguageClose}
              anchorOrigin={{vertical: 'top', horizontal: 'right'}}
            >
              <MenuItem onClick={() => {this.props.switchLanguage("spanish"); this.handleLanguageClose();}}><img style={styles.flagIconStyle} src={SpainFlag}/></MenuItem>
              <MenuItem onClick={() => {this.props.switchLanguage("english"); this.handleLanguageClose();}}><img style={styles.flagIconStyle} src={UKFlag}/></MenuItem>
            </Menu>
          </section>
        </Toolbar>
      </AppBar>
    );
  }
}

export default Navigation;
