import React from 'react';

//import Material UI
import Grid from '@material-ui/core/Grid';
import Button from "@material-ui/core/Button";
import StyledBadge from '@material-ui/core/Badge';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import UploadIcon from '@material-ui/icons/Backup';
import CloseIcon from "@material-ui/icons/Close";
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from "@material-ui/core/DialogActions";
import Dialog from '@material-ui/core/Dialog';
import Table from "@material-ui/core/Table";
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Fab from '@material-ui/core/Fab';
import { TableFooter } from '@material-ui/core';

const styles = {
  iconButton: {
    color: "white"
  },
  denseTableCell: {
    padding: "4px 5px 4px 5px"
  },
  toolbarFab : {
    margin: "3px"
  }
}

class TicketCart extends React.Component {
  constructor() {
    super();
    this.state = {
      ticketReportOpen: false,
      page: 0,
      cart: new Array()
    }
    this.handleHideReport = this.handleHideReport.bind(this);
    this.handleShowReport = this.handleShowReport.bind(this);
    this.handleChangePage = this.handleChangePage.bind(this);
  }

  handleChangePage(event, newPage) {
    console.log(newPage)
    this.setState({
      page: newPage
    });
  }

  handleHideReport() {
    this.setState({
      ticketReportOpen: false
    });
  }

  handleShowReport() {
    this.setState({
      cart: this.props.ticketCart,
      ticketReportOpen: true
    });
  }

  render() {
    const sendButton = (
      <div>
        <Fab
          style = {styles.toolbarFab}
          size="small"
          color="primary"
          disabled={this.props.ticketCart.length == 0}
          onClick={this.handleShowReport}
        >
          <StyledBadge badgeContent={this.props.ticketCart.length} color="error">
            <ShoppingCartIcon />
          </StyledBadge>
        </Fab>
        <Fab
          style = {styles.toolbarFab}
          size="small"
          variant="contained"
          color="primary"
          disabled={this.props.ticketCart.length == 0}
          onClick={this.props.requestPut}>
          <UploadIcon />
        </Fab>
      </div>
    );

    const report = (
      <Dialog onClose={this.handleHideReport} open={this.state.ticketReportOpen} aria-labelledby="simple-dialog-title">
        <DialogTitle>
          Selected tickets
        </DialogTitle>
        <Table>
          <TableBody>
            {
              this.state.cart.slice((this.state.page * 10), (this.state.page * 10) + 10).map(row => (
                <TableRow>
                  <TableCell style={styles.denseTableCell}>{row}</TableCell>
                </TableRow>
              ))}
          </TableBody>
          <TableFooter>
            <TablePagination
              rowsPerPageOptions={[10]}
              count={this.state.cart.length}
              rowsPerPage={10}
              page={this.state.page}
              onChangePage={this.handleChangePage}
            />
          </TableFooter>
        </Table>
        <DialogActions>
          <Button onClick={this.handleHideReport} variant="contained" color="secondary">
            <CloseIcon/>
          </Button>
          <Button onClick={this.props.requestPut} variant="contained" color="primary">
            <UploadIcon/>
          </Button>
        </DialogActions>
      </Dialog>
    );

    return (
      <div>
        <Grid
          container
          direction="row"
          justify="flex-end"
          alignItems="center"
        >
          {sendButton}
        </Grid>
        {report}
      </div>
    );
  }
}

export default TicketCart;
