import * as Constant from "../../app/helpers/constants";
import Model from "./model";

const call = function (callBack, requestId) {
  let url = Constant.URL_REQUEST_ENTITIES;
  url = url.replace(":requestId", requestId);
  console.clear();
  console.log(url);

  let myHeaders = new Headers();

  let dataSet = [];

  myHeaders.append('Accept', 'application/json');

  let myInit = {
    method: 'GET',
    headers: myHeaders,
  };

  fetch(url, myInit)
    .catch(error => {
      return error;
    })
    .then(res => {
      console.log(res.status);
      if (res.status != 200) {
        callBack(-1)
      }
      else {
        return res.json();
      }
    })
    .then(data => {
      try {
        data.entities.requestEntities.forEach(element => {
          let model = new Model(
            element.germplasmId,
            element.id,
            element.name,
            element.typeId
          );
          dataSet.push(model);
        });
        callBack(dataSet);
      } catch (error) {
        console.log(error);
        callBack(-1);
      }
    })

}

export default call;


