class Model{
  constructor(creation_date, description, id, entities){
    this._creation_date = creation_date;
    this._description = description;
    this._id = id;
    this._entities = entities;
  }

  get creation_date(){
    return this._creation_date;
  }
  set creation_date(new_creation_date){
    this._creation_date = new_creation_date;
  }

  get description(){
    return this._description;
  }
  set description(new_description){
    this._description = new_description;
  }

  get id(){
    return this._id;
  }
  set id(new_id){
    this._id = new_id;
  }

  get entities(){
    return this._entities
  }
  set entities(new_entities){
    this._entities = new_entities;
  }
}

export default Model;
