import React from 'react';

//import Material UI
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

//import components
import TicketCart from "./enhancedCart";

const styles = {
  appBar: {
    marginBottom: "0em"
  },
  rightSection: {
    marginLeft: 'auto',
    marginRight: 0
  }
}

class ToolBar extends React.Component {
  constructor() {
    super();
    this.state = {
      componentName: "requestToolBar",
      requestTableTicketCheckList: new Array(),
      ticketCart: [],
      idCart: []
    }
  }
  render() {
    return (
      <AppBar color="secondary" style={styles.appBar} elevation="0" position="static">
        <Toolbar variant="dense">
          <Typography variant="h5">{this.props.containerName}</Typography>
          <section style={styles.rightSection}>
          <TicketCart
            ticketCart={this.props.ticketCart}
            showReport={this.showTicketReport}
            translations={this.props.translations}
            requestPut={this.props.requestPut}
          />
          </section>
        </Toolbar>
      </AppBar>
    );
  }
}

export default ToolBar;
