import * as Constant from "../../app/helpers/constants";
import Model from "./model";

const call = (callback, language) => {

  let url = null;

  switch (language) {
    case "english":
      url = Constant.URL_TRANSLATIONS_ENGLISH;
      break;
    case "spanish":
      url = Constant.URL_TRANSLATIONS_SPANISH;
      break;
    default:
      break;
  }
  let myHeaders = new Headers();

  myHeaders.append('Accept', 'application/json');

  let myInit = {
    method: "GET",
    headers: myHeaders
  }

  fetch(url, myInit)
    .catch(error => {
      return error
    })
    .then(response => {
      console.log(response.status);
      if (response.status != 200) {
        callback(-1);
      }
      else {
        return (response.json())
      }
    })
    .catch(error => {
      return error;
    })
    .then(json => {
      //make sure object is well formed
      if(json == undefined){
        console.warn("translation object is not well formed");
        callback(-1);
      }
      let model = new Model(
        json.request,
        json.batch,
        json.design,
        json.shipment,
        json.selectedTickets,
        json.loading,
        json.information,
        json.dataSuccess,
        json.error,
        json.dataError,
        json.select,
        json.ticket,
        json.client,
        json.service,
        json.subService,
        json.crop,
        json.nEntities,
        json.comments,
        json.date,
        json.close,
        json.okay,
        json.welcome
      );
      callback(model)
    });
}

export default call;
