import * as Constant from "../../app/helpers/constants";
import Model from "./model";

const call = function (callBack, page) {
  let url = Constant.URL_REQUEST_CATALOG + page;
  console.log(url);
  let dataSet = [];

  var myHeaders = new Headers();

  myHeaders.append('Accept', 'application/json');

  var myInit = {
    method: 'GET',
    headers: myHeaders,
  };

  fetch(url, myInit)
    .catch((error) => {
      return(error);
    })
    .then((response) => {
      console.log(response.status)
      if(response.status != 200){
        callBack (-1);
      }
      else{
        return (response.json());
      }
    })
    .then((data) => {
      try {
        data.entities.requests.forEach(element => {
          let modelObject = new Model(
            element.comments,
            element.creationDate,
            element.cropId,
            element.firstName,
            element.id,
            element.lastName,
            element.serviceId,
            element.subserviceId, 
            element.ticketNumber,
            element.totalEntities,
            element.userId
          );
          dataSet.push(modelObject);
        });
        callBack(dataSet);
      } catch (error) {
        console.warn(error);
        callBack(-1);
      }
    });
}

export default call;
