import * as Constant from "../../app/helpers/constants";

const call = function (callback, id) {
    let url = Constant.URL_REQUEST_PUT;
  
    let data = JSON.stringify({
      "request": {
        "statusId" : 2,
        "requestId" : id
      }
    })
  
    let otherParam = {
      headers : {
        "content-type":"application/json"
      },
      body:data,
      method:"PUT"
    };
  
    console.log(data)
  
    fetch(url, otherParam)
    .then(response=>{callback(response.status)})
    .catch(error=>{console.log(error); callback(-1)});
}

export default call;
